@import url(https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css" integrity="sha384-ggOyR0iXCbMQv3Xipma34MD+dH/1fQ784/j6cY/iJTQUOhcWr7x9JvoRxT2MZw1T);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

table {
  width: 500px;
  border: solid black 2px;
  margin: auto;
  margin-top: 3%;
  height: 50px;
}

.search {
  margin: auto;
  width: 500px;
}
.name {
  width: 300px;
  line-height: 50px;
}

img {
  float: left;
  padding-right: 15px;
  line-height: 50px;
  padding-top: 5px;
}

.bb {
  float: right;
  position: relative;
  margin-top: -45px;
  margin-right: 10px;
}

table td {
  border: solid black 1px;
}

